}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

a {
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
