.header {
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: 1fr auto;
  grid-template-areas: "info info" "logo nav";
  padding: 12px 0 94px;
}

.top-info {
  grid-area: info;
  display: flex;
  justify-content: space-between;
  padding-bottom: 5px;
}

.top-info__lang {
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  letter-spacing: 1px;
  color: #fff;
}

.top-info__lang-btn {
  cursor: pointer;
  /*display: flex;*/
  align-items: center;
  border: none;
  background-color: transparent;
  border-radius: 8px;
  padding: 3px 5px;
}

.active, .top-info__lang-btn:hover {
  background: #F0145A;
}

.top-info__lang-btn-text {
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  letter-spacing: 1px;
  color: #B3B3BA;
}

.active, .top-info__lang-btn-text:hover {
  color: #fff;
}

.top-info__ph-num {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #B3B3BA;
}

.top-info__ph a {
  text-decoration: none;
}

.top-info__ph a:hover {
  color: #F0145A;
}

.top-info__ad-text {
  font-size: 16px;
  line-height: 19px;
  text-align: right;
  color: #fff;
}

.top-info__ad-text::before,
.top-info__ad-text::after {
  content: url("../img/dot.svg");
  padding-right: 7px;
  padding-left: 7px;
}

.top-info__fb-ic::before {
  content: url("../img/dot.svg");
  padding-right: 10px;
  padding-left: 10px;
}

.logo-box {
  grid-area: logo;
}

.box {
  display: inline-flex;
  /*align-items: center;*/
  margin-left: 2px;
}

.logo {
  text-decoration: none;
}

.box a {
  font-weight: normal;
  font-size: 19px;
  line-height: 22px;
  color: #B3B3BA;
  text-decoration: none;
  align-items: center;
}

.box__pub::after {
  content: url("../img/p-logo.svg");
  padding-left: 2px;
  align-items: center;
}

.box__pub::before {
  content: url("../img/slash-logo.svg");
  width: 11px;
  height: 24px;
  padding-right: 2px;
  align-items: center;
}

.menu {
  grid-area: nav;
