.down-bg {
  background: linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643E72 87.46%);
  border-radius: 0 0 12px 12px;
  position: relative;
}

.down-bg::after {
  content: " ";
  height: 10px;
  position: absolute;
  left: 20px;
  right: 20px;
  bottom: -10px;
  border-radius: 0 0 12px 12px;
  background-color: #CACADB;
}

.down-bg::before {
  content: " ";
  height: 10px;
  position: absolute;
  left: 40px;
  right: 40px;
  bottom: -20px;
  border-radius: 0 0 12px 12px;
  background-color: #E7E7F2;
}

