.publication {
  padding: 17px 0 20px 0;
  background: #F5F5FA;
}

.publication__a {
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #24A3FF;
  text-decoration: none;
}

.publication__title {
  padding-top: 3px;
  font-weight: bold;
  font-size: 32px;
  line-height: 38px;
  color: #F0145A;
  padding-bottom: 25px;
}

.publication__card {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  row-gap: 28px;
  /*margin: 0 -20px;*/
}

.publication__card-item {
  width: 100%;
  /*padding: 0 20px;*/
}

.publication__card-item-box {
  background: #fff;
  border-radius: 8px;
}

.publication__card-item-box-img {
  padding-bottom: 16px;
}

.publication__card-item-box-img-img img {
  min-width: 100%;
}

.publication__card-item-box-text {
  padding-left: 22px;
}

.publication__card-item-box-text-p {
  padding-top: 5px;
  padding-bottom: 19px;
}

.card-desk {
  display: none;
}

.publication__button {
  justify-content: center;
  display: flex;
  padding-top: 35px;
  padding-bottom: 43px;
  border-bottom: 4px solid #E6E6F0;
}

.publication__button-btn {
  background-color: #24A3FF;
  padding: 15px 76px;
  box-shadow: 0px 0px 20px rgba(36, 163, 255, 0.5);
  border-radius: 12px;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #fff;
  box-sizing: border-box;
  border: none;
}

.publication__button-btn:hover {
  background: #0085FF;
  box-shadow: 0px 0px 30px rgba(36, 163, 255, 0.5);
}

@media (min-width: 992px) {
  .card-desk {
    display: flex;
    margin: 0 -20px;
    /*row-gap: 40px;*/
  }
  .card-mob {
    display: none;
  }
  .publication {
    padding-top: 40px;
    padding-bottom: 17px;
  }
  .publication__card {
    row-gap: 20px;
  }
  .publication__card-item {
    width: 33.333%;
    padding: 0 20px 20px;
  }
  .publication__card-item-box {
    height: 100%;
    display: flex;
    flex-direction: column;
    column-gap: 40px;
  }
  .publication__card-item-box-text {
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
  }
  .publication__card-item-box-text-p {
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 1px;
  }
  .publication__card-item-box-descr {
    padding-top: 20px;
    border-top: 4px solid #E6E6F0;
    font-size: 16px;
    line-height: 22px;
    color: #000000;
    margin-left: 22px;
    margin-right: 20px;
    padding-bottom: 20px;
  }
  .publication__card-item-box-img-img img {
    max-width: 100%;
  }
  .publication__title {
    padding-bottom: 40px;
  }
  .publication__button {
    padding-top: 35px;
    padding-bottom: 52px;
  }
}

.footer {
  background: #F5F5FA;
}

.footer__info {
  display: grid;
  grid-template-rows: auto auto auto;
  grid-template-areas: "add" "contact" "flogo";
  padding-bottom: 118px;
}

.footer__info-loc {
  grid-area: add;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.footer__info-add {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #323264;
}

.footer__info-contact {
  grid-area: contact;
  flex-direction: column;
  justify-content: center;
}

.footer__info-map {
  text-align: center;
  padding-top: 4px;
  padding-bottom: 20px;
}

.footer__info-map__a {
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 1px;
  color: #24A3FF;
  text-decoration: none;
  text-transform: uppercase;
}

.footer__info-map__a:hover {
  color: #F0145A;
}

.footer__info-fb {
  grid-area: fb;
  text-align: center;
}

.footer__info-fb__icon {
  text-decoration: none;
}

.footer__info-fb__icon :hover path {
  fill: #F0145A;
}

.footer__info-ph {
  grid-area: ph;
  text-decoration: none;
  text-align: center;
  padding-top: 14px;
  padding-bottom: 20px;
}

.footer__info-ph__a {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #46466E;
  text-decoration: none;
}

.footer__info-ph:hover {
  color: #F0145A;
}

.footer__info-em {
  grid-area: em;
  text-align: center;
  padding-bottom: 20px;
}

.footer__info-logo {
  grid-area: flogo;
}

.footer__info-em__a {
  text-decoration: none;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #24A3FF;
}

.footer__info-em__a:hover {
  color: #F0145A;
}

.footer__info-logo__p {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #B3B3BA;
}

@media (min-width: 768px) {
  .footer__info {
    grid-template-rows: auto auto auto;
    grid-template-areas: "add" "contact" "flogo";
    padding-top: 12px;
    padding-bottom: 81px;
  }
  .footer__info-loc {
    flex-direction: row;
    justify-content: center;
  }
  .footer__info-contact {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .footer__info-fb svg {
    align-self: center;
  }
  .footer__info-map {
    padding-top: 0;
  }
  .footer__info-ph {
    padding-top: 0;
    padding-bottom: 0;
  }
  .footer__info-ph::before,
  .footer__info-ph::after {
    content: url("../img/foot-dot.svg");
    padding-right: 10px;
    padding-left: 10px;
  }
  .footer__info-map::before {
    content: url("../img/foot-dot.svg");
    padding-left: 10px;
    padding-right: 10px;
  }
  .footer__info-em {
    padding-bottom: 0;
  }
}

@media (min-width: 992px) {
  .footer__info {
    grid-template-rows: auto auto;
    grid-template-columns: auto auto;
    grid-template-areas: "add flogo" "contact contact";
    padding-top: 7px;
    padding-bottom: 40px;
  }
  .footer__info-loc {
    justify-content: flex-start;
  }
  .footer__info-contact {
    justify-content: flex-start;
    padding-top: 5px;
  }
  .footer__info-add {
    font-weight: 500;
    padding-right: 22px;
  }
  .footer__info-logo {
    display: flex;
    justify-content: flex-end;
  }
}
